import React from 'react';
import { NavLink } from 'react-router-dom';
import s from './Dialogs.module.css'


const DialogItem = (props) => {
    let path ="dialogs/*" + props.id
    return <div className={s.dialog + ' ' + s.active}>
        <NavLink to={path}>{props.name}</NavLink>
    </div>
}

const Dialogs = (props) => {
    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                <DialogItem name="Dmitrii" id="1" />
                <DialogItem name="Valera" id="2" />
                <DialogItem name="Dasha" id="3" />
                <DialogItem name="Artem" id="4" />
                <DialogItem name="Sveta" id="5" />
            </div>

            <div className={s.massages}>
                <div className={s.dialog}>Hi! How are you?</div>
                <div className={s.dialog}>Hi. It's okay. And you?</div>
                <div className={s.dialog}>I'm fine</div>
            </div>

        </div >


    )
}


export default Dialogs;